import Vue from 'vue'
import VueRouter from 'vue-router'


import Login from '../views/Login.vue'
import Register from '../views/Register.vue'

import Account from '../views/accounts/Account.vue'

import Purchases from '../views/purchases/Purchases.vue'
import HistoryPurchases from '../views/purchases/HistoryPurchases.vue'
import Purchase from '../views/purchases/Purchase.vue'
import CreatePurchase from '../views/purchases/CreatePurchase.vue'
import ChangePurchase from '../views/purchases/ChangePurchase.vue'
import HistoryChangePurchase from '../views/purchases/HistoryChangePurchase.vue'

import Chats from '../views/chats/Chats.vue'
import Chat from '../views/chats/Chat.vue'


import AdminUsers from  '../views/admins/Users'
// import AdminUser from '../views/admins/User'
// import AdminPurchases from '../views/admins/Purchases'
// import AdminPurchase from '../views/admins/Purchase'
import AdminHandbooks from '../views/admins/Handbooks'


import PageError404 from '../views/404'

import store from '../store'

Vue.use(VueRouter);

const routes = [
    {
        path: '/login',
        name: '',
        component: Login,
        meta: {
            IS_GUEST: true
        }
    },
    {
        path: '/register',
        name: '',
        component: Register,
        meta: {
            IS_GUEST: true
        }
    },
    {
        path: '/',
        name: ' ',
        component: Purchases,
        meta: {
            IS_LOGIN: true
        }
    },
    {
        path: '/account',
        name: ' ',
        component: Account,
        meta: {
            IS_LOGIN: true
        }
    },
    {
        path: '/view/purchase/:user/:id',
        name: ' ',
        component: Purchase,
        meta: {
            IS_LOGIN: true
        }
    },
    {
        path: '/create/purchase',
        name: '  ',
        component: CreatePurchase,
        meta: {
            IS_LOGIN: true
        }
    },
    {
        path: '/history/purchases',
        name: ' ',
        component: HistoryPurchases,
        meta: {
            IS_LOGIN: true
        }
    },
    {
        path: '/change/purchase/:user/:id',
        name: ' ',
        component: ChangePurchase,
        meta: {
            IS_LOGIN: true
        }
    },
    {
        path: '/history/change/purchase/:user/:id',
        name: '  ',
        component: HistoryChangePurchase,
        meta: {
            IS_LOGIN: true
        }
    },
    {
        path: '/chats',
        name: '',
        component: Chats,
        meta: {
            IS_LOGIN: true
        }
    },
    {
        path: '/chat/:id',
        name: '',
        component: Chat,
        meta: {
            IS_LOGIN: true
        }
    },
    {
        path: '/admin',
        name: ' ',
        component: AdminUsers,
        meta: {
            IS_LOGIN: true,
            IS_ADMIN: true
        }
    },
    {
        path: '/admin/user/:id',
        name: ' ',
        component: Account,
        meta: {
            IS_LOGIN: true,
            IS_ADMIN: true
        }
    },
    {
        path: '/admin/purchases/:id',
        name: ' ',
        component: Purchases,
        meta: {
            IS_LOGIN: true,
            IS_ADMIN: true
        }
    },
    {
        path: '/admin/history/purchases/:id',
        name: '  ',
        component: HistoryPurchases,
        meta: {
            IS_LOGIN: true,
            IS_ADMIN: true
        }
    },
    {
        path: '/admin/purchases',
        name: ' ',
        component: Purchases,
        meta: {
            IS_LOGIN: true,
            IS_ADMIN: true
        }
    },
    {
        path: '/admin/view/purchase/:user/:id',
        name: ' ',
        component: Purchase,
        meta: {
            IS_LOGIN: true,
            IS_ADMIN: true
        }
    },
    {
        path: '/admin/history/change/purchase/:user/:id',
        name: '  ',
        component: HistoryChangePurchase,
        meta: {
            IS_LOGIN: true,
            IS_ADMIN: true
        }
    },
    {
        path: '/admin/handbooks',
        name: ' ',
        component: AdminHandbooks,
        meta: {
            IS_LOGIN: true,
            IS_ADMIN: true
        }
    },
    {
        path: '/admin/create/purchase',
        name: '  ',
        component: CreatePurchase,
        meta: {
            IS_LOGIN: true,
            IS_ADMIN: true
        }
    },
    {
        path: '/admin/change/purchase/:user/:id',
        name: ' ',
        component: ChangePurchase,
        meta: {
            IS_LOGIN: true,
            IS_ADMIN: true
        }
    },
    {
        path: '*',
        name: '404',
        component: PageError404
    }
];

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
});


router.beforeEach((to, from, next) => {
    document.title = `${to.name} - ${store.getters.GET_SYSTEM.name}`;

    if (to.matched.some(record => record.meta.IS_LOGIN)) { //          

        if (store.getters.IS_LOGIN) { //     

            if (to.matched.some(record => record.meta.IS_ADMIN)) { //          

                if (store.getters.IS_ADMIN) { //         
                    return next()

                } else {
                    return next('/')
                }

            } else {
                return next()
            }

        } else {
            next('/login') //    ,      
        }
    } else if (to.matched.some(record => record.meta.IS_GUEST)) { //       
        if (!store.getters.IS_LOGIN) { //     
            next()
        } else {
            next('/') //         
        }
    } else {
        next() //          
    }
})


export default router
